.user {
	background-color:#fff;
	width: 40%;
	display: flex;
	align-items: flex-start;
	flex-wrap: wrap;
	margin-bottom: 50px;
	text-overflow: ellipsis;
	padding:0 20px;
	box-shadow: 0 8px 38px #ccc, 0 5px 12px #ccc;
}

.user_columns {
	width:30%;
	margin-left:20px;
	box-sizing: border-box;
	text-overflow: ellipsis;
}

.user_columns .user_detail {
	text-overflow: ellipsis;
	overflow: hidden;
}

.user_picture {
	width:50px;
	height:50px;
	margin-top:20px;
	position:relative;
}

.user h4 {
	font-size: 20px;
	font-weight: 600;
}

@media screen and (min-width:320px) and (max-width:667px) {
	.user {
		width:100%;
	}
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait) {
	.user {
		width:100%;
		flex-direction: column;
	}
	.user_columns {
		width:100% !important;
	}
	.user_picture {
		top:0;
		left:0;
		width:100px;
		height:100px;
		text-align: center;
	}

}